
/* chat container */
.chat {
  height: 100%;
  display: grid;
  grid-template-rows: 1fr 50px;
  grid-template-areas:
  "chatdialog"
  "bottom";
}

.chat-dialog {
  grid-area: "chatdialog";
  overflow-y: auto;
}

.chat-bottom {
  grid-area: "bottom";
  background-color: steelblue;
  border-top: 1px solid lightgrey;
}


/* chat dialog list */
.chat-dialog-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}


/* chat dialog list container */
.chat-container-left {
  display: grid;
  grid-template-columns: minmax(auto, 460px) minmax(20px, auto);
  grid-template-areas:
  "item gap";
  justify-items: start;
}

.chat-container-right {
  display: grid;
  grid-template-columns: minmax(20px, auto) minmax(auto, 460px);
  grid-template-areas:
  "gap item";
  justify-items: end;
}

.chat-container-left div {
  background-color: orangered;
  color: white;
}

.chat-container-right div {
  background-color: lightgrey;
  color: black;
}


/* chat dialog list container items */
/* type: text */
.chat-item {
  grid-area: item;
  display: grid;
  grid-template-rows: auto;
  grid-template-areas:
  "text"
  "time";
  margin: 5px 5px 5px 5px;
}

.chat-item-text {
  grid-area: text;
  margin: 10px 10px 0px 10px;
  font-size: 13px;
}

.chat-item-time {
  grid-area: time;
  justify-self: end;
  margin: 3px 10px 5px 0px;
  font-size: 10px;
  color: grey;
}

.speech-bubble {
  -webkit-border-radius: 4px;
  border-radius: 4px;
  line-height: 1.3;
}

.speech-bubble-left {
    border-left: 2px solid beige;
    border-bottom: 2px solid beige;
}

.speech-bubble-right {
    border-right: 2px solid grey;
    border-bottom: 2px solid grey;
}

/* type: image */
.chat-image {
  grid-area: item;
  margin: 5px 5px 5px 5px;
  max-width: 100%;
  height: auto;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  position: relative;
}
